@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondenced-Regular.woff2") format("woff2"), url("../fonts/RobotoCondenced-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 22px;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: normal;
  font-style: normal; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden; }

.button {
  padding: 9px 23px 9px 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #FFFFFF;
  border: 1px solid #FFFFFF;
  border-radius: 5px;
  cursor: pointer; }
  .button:hover {
    color: #00B2FF;
    background-color: #FFFFFF; }

.page-header__logo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  text-decoration: none;
  cursor: pointer; }

.logo__img {
  margin-right: 16px; }

.logo__text {
  margin-top: 8px; }

.logo__text-name {
  color: #FFFFFF;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  font-weight: 700; }
  @media (max-width: 320px) {
    .logo__text-name {
      display: none; } }

.logo__text-description {
  color: #788891;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 500; }
  @media (max-width: 320px) {
    .logo__text-description {
      display: none; } }

.page-header {
  background-color: #1b2d37;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.page-header__wrapper {
  width: 1512px;
  margin: 33px auto 0;
  padding: 0 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-items: space-between;
  color: #FFFFFF; }
  @media (max-width: 1023px) {
    .page-header__wrapper {
      margin-top: 20px;
      padding: 0 23px 20px 25px; } }
  @media (max-width: 320px) {
    .page-header__wrapper {
      padding: 0 17px 0 17px;
      margin-top: 17px; } }

.page-header__nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-right: 2px; }
  @media (max-width: 1023px) {
    .page-header__nav {
      width: 85px; } }
  @media (max-width: 320px) {
    .page-header__nav {
      height: 20px; } }

.page-header__mail {
  position: relative; }
  .page-header__mail::before {
    position: absolute;
    content: "";
    background-image: url("../img/envelope.svg");
    background-repeat: no-repeat;
    background-size: contain;
    width: 19px;
    height: 14px;
    left: 0;
    top: 4px; }
  @media (max-width: 1023px) {
    .page-header__mail::before {
      width: 25px;
      height: 19px; } }

.page-header__call {
  position: relative; }
  .page-header__call::before {
    position: absolute;
    content: "";
    background-image: url("../img/phone_call.svg");
    background-repeat: no-repeat;
    background-size: contain;
    width: 18px;
    height: 18px;
    left: 0;
    top: 2px; }
  @media (max-width: 1023px) {
    .page-header__call::before {
      width: 24px;
      height: 24px; } }

.page-header__mail a,
.page-header__call a {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  font-weight: 400;
  text-decoration: none;
  color: #FFFFFF; }
  @media (max-width: 1023px) {
    .page-header__mail a,
    .page-header__call a {
      display: none; } }

.page-header__call {
  margin-left: 60px; }

.page-header__callback {
  max-width: 185px;
  margin-left: 50px; }
  @media (max-width: 1023px) {
    .page-header__callback {
      display: none; } }

.page-main__wrapper {
  margin: -90px 0 0;
  background-color: #1b2d37;
  background-image: url("../img/bg-pattern.png"); }

.page-main__text {
  padding-top: 200px; }
